import { useEffect, useState } from 'react'
import './App.css'
import {
  Toolbar,
  AppBar,
  Typography,
  Select,
  MenuItem,
  Grid,
  InputLabel,
  FormControl,
  Card,
  CardContent
} from '@mui/material'

// relative path
const url = `${window.location.protocol}//${window.location.hostname}:3002`

let map: google.maps.Map

function buildContent(point:{ latitude: number, longitude: number, timestamp: string }, index: number) {
  const timestampString = point.timestamp

  const date = new Date(timestampString);
  
  const optionsDate = { year: 'numeric', month: 'long', day: 'numeric' };
  const optionsTime = { hour: '2-digit', minute: '2-digit', second: '2-digit' };
  
  // @ts-ignore
  const formattedDate = date.toLocaleString('en-US', optionsDate);
  // @ts-ignore
  const formattedTime = date.toLocaleString('en-US', optionsTime);
  

  const content = document.createElement("div")
  content.classList.add("property")
  content.innerHTML = `
  <div>
    <div style="
      background-color: ${index ? '#ff9800' : '#4285F4'};
      border-radius: 8px;
      color: #FFFFFF;
      font-size: 14px;
      padding: 10px 15px;
      position: relative;
    ">
      Location ${index}
    </div>
    <div style="
      position: absolute;
      left: 100px;
      bottom: 4px;
      font-size: larger;
      width: 100px
    "
    >
      <div>${formattedDate}</div>
      <div>${formattedTime}</div>

    </div>
  </div>
  `
  return content
}

async function updateMap(session_id: string): Promise<void> {
  const { Map: GoogleMap, LatLngBounds, Polyline, marker: { AdvancedMarkerElement }, SymbolPath } = google.maps

  const response = await fetch(`${url}/points?id=${session_id}`)
  const data: { latitude: number, longitude: number, timestamp: string }[] = await response.json()

  map = new GoogleMap(document.getElementById("google-map") as HTMLElement, {
    center: {
      lat: 52.133291,
      lng: -106.670135
    },
    zoom: data.length ? 40 : 5,
    mapId: 'DEMO_MAP_ID',
  })

  const bounds = new LatLngBounds()

  data.forEach((point, index) => {
    const { latitude, longitude } = point
    new AdvancedMarkerElement({
      map,
      position: { lat: latitude, lng: longitude },
      content: buildContent(point, index),

    })
    bounds.extend({
      lat: latitude,
      lng: longitude
    })
  })

  if (data.length) {
    map.fitBounds(bounds)
  }

  const path = new Polyline({
    path: data.map((point) => ({
      lat: point.latitude,
      lng: point.longitude,
    })),
    strokeColor: 'red',
    strokeOpacity: 1.0,
    strokeWeight: 2,
    geodesic: true,
    icons: [{
      icon: { path: SymbolPath.FORWARD_CLOSED_ARROW },
      repeat: '100px'
    }]
  })

  path.setMap(map)
}


function App() {

  const [routes, set_routes] = useState<{ route_session_id: string, route_session_type: string, user_id: string, trip_id: string }[]>([])

  const [current_route_item, set_current_route_item] = useState<{ route_session_id: string, route_session_type: string, user_id: string, trip_id: string } | undefined>()

  useEffect(() => {
    (async () => {
      await google.maps.importLibrary('marker')
      await google.maps.importLibrary('maps')

      updateMap('none')
    })()
  }, [])

  useEffect(() => {
    (async () => {
      const response = await fetch(`${url}/routes`)
      const data: { route_session_id: string, route_session_type: string, user_id: string, trip_id: string }[] = await response.json()
      set_routes(data)
    })()
  }, [])


  return <>
    <AppBar position="sticky">
      <Toolbar>
        <Typography variant="h6" component="div">
          Google Map App - Check your locations and path
        </Typography>

      </Toolbar>
    </AppBar>

    <Grid container spacing={2} style={{ height: '100%' }}>
      <Grid item xs={2}>
        <div style={{
          paddingLeft: 20,
          paddingTop: 10,
          display: 'flex',
          flexDirection: 'column',
          height: '100%'
        }}>
          <FormControl sx={{ width: "100%" }}>
            <InputLabel id="demo-simple-select-label">SessionId</InputLabel>
            <Select
              sx={{ width: '100%' }}
              onChange={(event) => {
                let route_session_id = event.target?.value as string
                updateMap(route_session_id)

                set_current_route_item(routes.find(route => route.route_session_id === route_session_id))
              }}
            >
              {
                routes.map(({ route_session_id }) => {
                  return <MenuItem value={route_session_id}>{route_session_id}</MenuItem>
                })
              }
            </Select>
          </FormControl>
          <div style={{ flex: 1 }}></div>
          {
            (() => {
              if (!current_route_item)
                return

              const { route_session_id, route_session_type, user_id, trip_id } = current_route_item


              return <Card>
                <CardContent>
                  <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    Session Id
                  </Typography>

                  <Typography variant="h6" component="div">
                    {
                      route_session_id
                    }
                  </Typography>
                  <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    Session Type
                  </Typography>

                  <Typography variant="h6" component="div" style={{
                    overflow: 'auto'
                  }}>
                    {
                      route_session_type
                    }
                  </Typography>

                  <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    User Id
                  </Typography>

                  <Typography variant="h6" component="div" style={{
                    overflow: 'auto'
                  }}>
                    {
                      user_id
                    }
                  </Typography>

                  <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    Trip Id
                  </Typography>

                  <Typography variant="h6" component="div" style={{
                    overflow: 'auto'
                  }}>
                    {
                      trip_id
                    }
                  </Typography>
                </CardContent>
              </Card>
            })()
          }

        </div>
      </Grid>
      <Grid item xs={10}>
        <div id='google-map' style={{
          width: '100%',
          height: '100%'
        }} />
      </Grid>
    </Grid>
  </>
}

export default App
